.StepButtons {
  position: fixed;
  bottom: 0;
  width: 100%;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.25);
  box-shadow: 0px 0px 3px 0px rgba(0,0,0,0.25);
  z-index: 100;
  background: white;
}

.StepButtons a.cta {
  margin-left: 24px;
  margin-right: 24px;
  margin-top: 12px;
  margin-bottom: 12px;
  display: inline-block;
}

.StepButtons a.cta.next {
  float: right;
}


